<template>
	<Tabs v-model="tab" class="tab" @on-click="toggleTab">
		<Tab-pane label="待确认" name="confirm" class="tab-item">
			<search-box @search="searchHistory" :refreshFlag="refreshFlag" :itemList="confirmTab.itemList"></search-box>
			<Table highlight-row :columns="confirmTab.confirmHeader" :data="confirmTab.confirmList" size="large"></Table>
			<div class="page">
				<Page placement='top' :total="confirmTab.totalPage" :current="confirmTab.searchParams.currentPage" :page-size="confirmTab.searchParams.pageSize"
					  :page-size-opts="[10,20,30,40]"
					  show-elevator show-sizer show-total
					  @on-change="pageChange" @on-page-size-change="pageSizeChange"
				></Page>
			</div>
		</Tab-pane>
		<Tab-pane label="历史记录" name="history" class="tab-item">
			<search-box @search="searchHistory" :refreshFlag="refreshFlag" :itemList="historyTab.itemList" stage="complete"></search-box>
			<Table size="large" :columns="historyTab.historyHeader" :data="historyTab.historyList"></Table>
			<div class="page">
				<Page placement='top' :total="historyTab.totalPage" :current="historyTab.searchParams.currentPage"
					  :page-size="historyTab.searchParams.pageSize" :page-size-opts="[10,20,30,40]"
					  show-elevator show-sizer show-total
					  @on-change="pageChange" @on-page-size-change="pageSizeChange"
				></Page>
			</div>
		</Tab-pane>
		<i-button type="text" slot="extra" class="extra-btn" icon="ios-loop-strong" :disabled="refreshFlag" @click="refresh">刷新</i-button>
	</Tabs>
</template>
<script>
	import searchBox from '@/components/searchbox.vue'
	export default {
		data () {
			return {
				confirmTab: {
					confirmHeader: [
						{
							type: 'index',
							title: '序号',
							align: 'center'
						},
						{
							title: '放款渠道',
							key: 'channelCode',
							align: 'center',
							render:(h,params)=>{
								let code=params.row.channelCode;
								let channelName='';
								switch (code) {
									case 1802:
										channelName = '外贸信托';
										break;
									case 1804:
										channelName = '新网银行';
										break;
									default:
										channelName = '未知渠道';
								}
								return h('span',channelName)
							}
						},
						{
							title: '贷款客户',
							key: 'name',
							align: 'center'
						},
						{
							title: '贷款产品',
							key: 'eProduct',
							align: 'center'
						},
						{
							title: '业务城市',
							key: 'city',
							align: 'center'
						},
						{
							title: '初审贷款金额',
							key: 'appMoney',
							align: 'center',
							render: (h, params) => {
								let _this = this;
								return h('span', _this.fmoney(params.row.appMoney))
							}
						},
						{
							title: '利率（年）',
							key: 'rate',
							align: 'center',
							render:(h,params)=>{
								return h('span',`${params.row.rate}%`)
							}
						},
						{
							title: '抵押物评估',
							key: 'nbComfirmValue',
							align: 'center',
							render: (h, params) => {
								let _this = this;
								return h('span', _this.fmoney(params.row.nbComfirmValue))
							}
						},
						{
							title: '抵押率',
							key: 'nbCRate',
							align: 'center',
							render:(h,params)=>{
								return h('span',`${params.row.nbCRate}%`)
							}
						},
						{
							title: '还款方式',
							key: 'repayWay',
							align: 'center'
						},
						{
							title: '接收时间',
							key: 'receiveTime',
							width:110,
							align: 'center',
							sortable: true,
							sortType:'desc',
							render: (h, params) => {
								return h('span',params.row.receiveTime ? new Date(params.row.receiveTime).Format("MM-dd hh:mm"):'-')
							}
						},
						{
							title: '操作',
							key: 'action',
							width: 100,
							align: 'center',
							render: (h, params) => {
								return h('i-button', {
									props: {
										type: 'primary',
										size: 'small',
									},
									style: {
										marginRight: '5px'
									},
									on: {
										click: () => {
											this.$router.push({
												name: 'orderloan',
												params: {
													loanId: params.row.loanId,
													serialNo:params.row.serialNo,
													nodeStatus:1006,
													tab: 'confirm'
												}
											})
										}
									}
								}, '账务确认')
							}
						}
					],
					confirmList: [],
					itemList:['aBaseName','channelCode'],
					totalPage: 10,
					searchParams:{
						currentPage:1,
						pageSize:10,
						nodeStatus:1006,
						flowStatus: 0,
					}
				},
				historyTab: {
					historyHeader: [
						{
							type: 'index',
							title: '序号',
							width: '80px',
							align: 'center'
						},
						{
							title: '放款渠道',
							key: 'channelCode',
							align: 'center',
							render:(h,params)=>{
								let code=params.row.channelCode;
								let channelName='';
								switch (code) {
									case 1802:
										channelName = '外贸信托';
										break;
									case 1804:
										channelName = '新网银行';
										break;
									default:
										channelName = '未知渠道';
								}
								return h('span',channelName)
							}
						},
						{
							title: '贷款客户',
							key: 'name',
							align: 'center'
						},
						{
							title: '贷款产品',
							key: 'eProduct',
							align: 'center'
						},
						{
							title: '业务城市',
							key: 'city',
							align: 'center'
						},
						{
							title: '初审贷款金额',
							key: 'appMoney',
							align: 'center',
							render: (h, params) => {
								let _this = this;
								return h('span', _this.fmoney(params.row.appMoney))
							}
						},
						{
							title: '利率（年）',
							key: 'rate',
							align: 'center',
							render:(h,params)=>{
								return h('span',`${params.row.rate}%`)
							}
						},
						{
							title: '还款方式',
							key: 'repayWay',
							align: 'center'
						},
						{
							title: '审核结果',
							key: 'flowStatus',
							align: 'center',
							render: (h, params) => {
								let _this = this;
								var flowStatus = params.row.flowStatus;
								return h('span', {
									style: {
										color: flowStatus == 1 ? '#19be6b' : '#ed3f14'
									}
								}, flowStatus == 1 ? '完成放款' : '拒绝放款')
							}
						},
						{
							title: '接收时间',
							key: 'receiveTime',
							align: 'center',
							render: (h, params) => {
								return h('span',params.row.receiveTime ? new Date(params.row.receiveTime).Format("MM-dd hh:mm"):'-')
							}
						},
						{
							title: '审核时间',
							key: 'auditingTime',
							width:111,
							align: 'center',
							sortable: true,
							sortType:'desc',
							render: (h, params) => {
								return h('span', params.row.auditingTime ? new Date(params.row.auditingTime).Format("MM-dd hh:mm"):'-')
							}
						},
						{
							title: '操作',
							key: 'action',
							align: 'center',
							width: 102,
							render: (h, params) => {
								return h('i-button', {
									props: {
										type: 'info',
										size: 'small',
									},
									style: {
										margin: '2px'
									},
									on: {
										click: () => {
											this.$router.push({
												name: 'loanconfirmdetail',
												params: {
													loanId: params.row.loanId,
													serialNo:params.row.serialNo,
													tab: 'history'
												}
											})
										}
									}
								}, '查看详情')
							}
						}
					],
					historyList: [],
					itemList:['aBaseName','channelCode','dateRange','status'],
					totalPage: 10,
					searchParams: {
						flowStatus: 5,
						currentPage: 1,
						pageSize: 10,
						nodeStatus:1006,
						loanId:1,
					}
				},
				refreshFlag:false,
				tab: 'confirm',
			}
		},
		components:{
			searchBox
		},
		created: function () {
			let tab = this.$route.params.tab;
			if (tab) {
				this.tab = tab;
			}
			this.loadList();
		},
		methods: {
			pageChange: function (val) {
				let _this = this;
				if (_this.tab == 'confirm') {//待审核切换页码
					if (_this.confirmTab.searchParams.currentPage != val) {
						_this.confirmTab.searchParams.currentPage  = val;
						_this.loadList();
					}
				} else if (_this.tab == 'history') {//历史记录
					if (_this.historyTab.searchParams.currentPage != val) {
						_this.historyTab.searchParams.currentPage = val;
						_this.loadList();
					}
				}
			},
			pageSizeChange: function (val) {
				let _this = this;
				if (_this.tab == 'confirm') {//待审核切换
					if (_this.confirmTab.searchParams.pageSize != val) {
						_this.confirmTab.searchParams.pageSize = val;
						_this.loadList();
					}
				} else if (_this.tab == 'history') {//历史记录
					if (_this.historyTab.searchParams.pageSize != val) {
						_this.historyTab.searchParams.pageSize = val;
						_this.loadList();
					}
				}
			},
			toggleTab: function (name) {
				this.loadList();
			},
			loadList: function () {
				let _this = this;
				_this.$Message.config({
					top: 200,
					duration: 2
				});
				clearTimeout(timer);
				_this.$Message.destroy();
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '数据请求中...',
						duration: 0
					});
				}, 500);
				if (_this.tab == 'confirm') {//待审核
					_this.$http.post('loanManage/loanCommon', _this.confirmTab.searchParams)
						.then(function (response) {
							var res = response.data;
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							if (res.code == 1) {//正常
								_this.$Message.success({
									content: res.msg
								});
								if(res.data.list){
									_this.confirmTab.totalPage = res.data.count;
									_this.confirmTab.searchParams.currentPage = res.data.currentPage;
									_this.confirmTab.searchParams.pageSize = res.data.pageSize;
									_this.confirmTab.confirmList = res.data.list;
								}
							} else {
								_this.$Message.error({
									content: res.msg
								});
							}
						})
						.catch(function (error) {
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							_this.$Message.error({
								content: error
							});
						});
				} else if (_this.tab == 'history') {//历史记录
					this.$http.post('/loanManage/loanCommon', _this.historyTab.searchParams)
						.then(function (response) {
							let res = response.data;
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							if (res.code == 1) {//正常
								_this.$Message.success({
									content: res.msg
								});
								if(res.data.list){
									_this.historyTab.totalPage = res.data.count;
									_this.historyTab.searchParams.currentPage = res.data.currentPage;
									_this.historyTab.searchParams.pageSize = res.data.pageSize;
									_this.historyTab.historyList = res.data.list;
								}
							} else {
								_this.$Message.error(res.msg);
							}
						})
						.catch(function (error) {
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							_this.$Message.error({
								content: error
							})
						});
				}
			},
			searchHistory: function (searchParams) {
				let _this = this;
				if (_this.tab == 'confirm') {//进行中
					_.assign(_this.confirmTab.searchParams,searchParams)
				}else if (_this.tab == 'history') {//历史记录
					_.assign(_this.historyTab.searchParams,searchParams)
				}
				_this.loadList();
			},
			refresh: function () {
				this.refreshFlag=true;
				this.loadList();
			}
		}
	}
</script>
<style>
	.tab {
		padding-top: 15px;
	}

	.ivu-tabs-tab {
		font-size: 16px;
	}

	.page {
		padding-top: 50px;
		display: flex;
		justify-content: flex-end;
		flex-direction: row;
		padding-bottom: 150px;
	}

	.layout-content .extra-btn {
		color: #20abff;
		font-size: 14px;
	}

	.layout-content .ivu-input-group-prepend {
		background-color: #20abff;
		border-color: #20abff;
		width: 80px;
	}

	.input-title {
		color: #fff;
		font-size: 14px;
	}

	.search-box {
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-items: center;
		padding-top: 14px;
		padding-bottom: 15px;
		flex-wrap: wrap;
	}

	.search-item {
		flex: 1;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		padding-bottom: 15px;
		margin-right: 10px;
	}

	.item-title {
		width: 80px;
		height: 36px;
		font-size: 14px;
		line-height: 36px;
		text-align: center;
		color: #fff;
		border-top-left-radius: 4px;
		border-bottom-left-radius: 4px;
	}

	.color-yellow {
		background-color: #f0b320;
	}

	.color-red {
		background-color: #ff7043;
	}

	.color-green {
		background-color: #28b779;
	}

	.search {
		width: 100px;
		margin-right: 10px;
	}

	.ivu-select-selection {
		border-top-left-radius: 0px;
		border-bottom-left-radius: 0px;
	}

	.search-item .ivu-input {
		border-top-left-radius: 0px;
		border-bottom-left-radius: 0px;
	}
</style>



